/* IPTVWebsite.module.css */

/* Variables globales (custom properties) */
:global(:root) {
    --primary-color: #1e90ff; /* Bleu vif pour les accents */
    --secondary-color: #2c3e50; /* Gris fonc√© pour texte principal */
    --background-color: #f5f7fa; /* Fond clair */
    --card-bg: #ffffff; /* Fond des cartes */
    --text-light: #ffffff; /* Texte clair */
    --success-color: #27ae60; /* Vert pour checks */
    --shadow: 0 4px 12px rgba(0, 0, 0, 0.1); /* Ombre douce */
    --transition: all 0.3s ease-in-out;
  }
  
  /* Reset et styles de base */
  .iptvWebsiteWrapper {
    font-family: 'Roboto', sans-serif;
    background-color: var(--background-color);
    color: var(--secondary-color);
    line-height: 1.6;
    overflow-x: hidden;
  }
  
  /* Conteneur g√©n√©rique */
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1.5rem;
  }
  
  /* Header */
  .header {
    position: sticky;
    top: 0;
    background-color: var(--card-bg);
    box-shadow: var(--shadow);
    z-index: 1000;
    padding: 1rem 0;
  }
  
  .header .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .logoLink {
    cursor: pointer;
  }
  
  .logoText {
    font-size: 1.8rem;
    font-weight: 700;
  }
  
  .logoTextSpan {
    color: var(--primary-color);
  }
  
  .navigation {
    display: flex;
    gap: 1.5rem;
  }
  
  .navButton {
    background: none;
    border: none;
    font-size: 1rem;
    font-weight: 500;
    color: var(--secondary-color);
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: var(--transition);
  }
  
  .navButton:hover,
  .navButton.active {
    color: var(--primary-color);
    border-bottom: 2px solid var(--primary-color);
  }
  
  /* Boutons g√©n√©riques */
  .whatsappButton,
  .offersButton,
  .orderButton,
  .ctaButton {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1.5rem;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
    transition: var(--transition);
  }
  
  .whatsappButton {
    background-color: #25d366;
    color: var(--text-light);
    border: none;
  }
  
  .whatsappButton:hover {
    background-color: #20b956;
  }
  
  .offersButton,
  .orderButton {
    background-color: var(--primary-color);
    color: var(--text-light);
    border: none;
  }
  
  .offersButton:hover,
  .orderButton:hover {
    background-color: #1874cd;
  }
  
  .buttonIcon {
    width: 1.2rem;
    height: 1.2rem;
  }
  
  /* Hero Section */
  .heroSection {
    background: linear-gradient(135deg, #1e90ff 0%, #2c3e50 100%);
    color: var(--text-light);
    padding: 6rem 0;
    position: relative;
    overflow: hidden;
  }
  
  .heroContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    align-items: center;
  }
  
  .heroContent {
    max-width: 600px;
  }
  
  .heroTitle {
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    animation: fadeInUp 1s ease-out;
  }
  
  .heroSubtitle {
    font-size: 1.2rem;
    opacity: 0.9;
    margin-bottom: 2rem;
  }
  
  .heroButtons {
    display: flex;
    gap: 1rem;
  }
  
  .heroImage {
    text-align: center;
  }
  
  .heroIcon {
    width: 200px;
    height: 200px;
    animation: float 3s ease-in-out infinite;
  }
  
  /* Animations */
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-20px); }
  }
  
  /* Sections g√©n√©riques */
  .servicesSection,
  .abonnementsSection,
  .appareilsSection,
  .faqSection,
  .securitySection,
  .ctaSection {
    padding: 5rem 0;
  }
  
  .sectionHeading {
    text-align: center;
    margin-bottom: 3rem;
  }
  
  .sectionTitle {
    font-size: 2rem;
    font-weight: 700;
    color: var(--secondary-color);
    margin-bottom: 1rem;
  }
  
  .sectionSubtitle {
    font-size: 1.1rem;
    max-width: 800px;
    margin: 0 auto;
    color: #666;
  }
  
  /* Card Grid */
  .cardGrid {
    display: grid;
    gap: 2rem;
  }
  
  .cardGrid3Cols {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
  
  .cardGrid2Cols {
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  }
  
  .card {
    background: var(--card-bg);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: var(--shadow);
    transition: transform 0.3s ease;
  }
  
  .card:hover {
    transform: translateY(-10px);
  }
  
  .cardIcon {
    width: 3rem;
    height: 3rem;
    margin-bottom: 1rem;
    color: var(--primary-color);
  }
  
  .cardTitle {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 1rem;
  }
  
  .card ul {
    list-style: none;
    padding: 0;
  }
  
  .card li {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.75rem;
  }
  
  .card li svg {
    width: 1rem;
    height: 1rem;
    color: var(--success-color);
  }
  
  /* Pricing Cards */
  .pricingCard {
    position: relative;
    text-align: center;
  }
  
  .price {
    font-size: 2rem;
    font-weight: 700;
    color: var(--primary-color);
    margin: 1rem 0;
  }
  
  .price span {
    font-size: 1rem;
    color: #666;
  }
  
  .popular {
    border: 2px solid var(--primary-color);
  }
  
  .popularBadge {
    position: absolute;
    top: -1rem;
    left: 50%;
    transform: translateX(-50%);
    background: var(--primary-color);
    color: var(--text-light);
    padding: 0.25rem 1rem;
    border-radius: 20px;
    font-size: 0.9rem;
  }
  
  /* Device Grid */
  .deviceGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
    margin-bottom: 3rem;
  }
  
  .deviceItem {
    text-align: center;
  }
  
  .deviceIconWrapper {
    width: 4rem;
    height: 4rem;
    margin: 0 auto 1rem;
    color: var(--primary-color);
  }
  
  /* Config Box */
  .configBox {
    background: var(--card-bg);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: var(--shadow);
  }
  
  .configGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  
  .configColumn h4 {
    font-size: 1.25rem;
    margin-bottom: 1rem;
  }
  
  /* Security Section */
  .securityGrid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
  
  .securityItem {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .securityIconWrapper {
    width: 2rem;
    height: 2rem;
    color: var(--primary-color);
  }
  
  .greenCheck {
    color: var(--success-color);
  }
  
  .trustBox {
    background: var(--card-bg);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: var(--shadow);
  }
  
  .trustListItem {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.75rem;
  }
  
  /* CTA Section */
  .ctaSection {
    background: var(--primary-color);
    color: var(--text-light);
    text-align: center;
  }
  
  .ctaTitle {
    font-size: 2rem;
    font-weight: 700;
  }
  
  .ctaSubtitle {
    font-size: 1.2rem;
    margin: 1rem 0 2rem;
  }
  
  .ctaButton {
    background: var(--text-light);
    color: var(--primary-color);
    font-size: 1.1rem;
  }
  
  .ctaButton:hover {
    background: #e0e0e0;
  }
  
  /* Footer */
  .footer {
    background: var(--secondary-color);
    color: var(--text-light);
    padding: 3rem 0 1rem;
  }
  
  .footerGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
    margin-bottom: 2rem;
  }
  
  .footerLogo {
    font-size: 1.5rem;
    font-weight: 700;
    cursor: pointer;
  }
  
  .footerLogo span {
    color: var(--primary-color);
  }
  
  .footerHeading {
    font-size: 1.25rem;
    margin-bottom: 1rem;
  }
  
  .footer ul {
    list-style: none;
    padding: 0;
  }
  
  .footer ul li button,
  .footer ul li span {
    color: var(--text-light);
    background: none;
    border: none;
    padding: 0;
    text-align: left;
    font-size: 1rem;
    margin-bottom: 0.75rem;
    cursor: pointer;
    transition: var(--transition);
  }
  
  .footer ul li button:hover {
    color: var(--primary-color);
  }
  
  .disabledLink {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  .footerWhatsappButton {
    margin: 1rem 0;
  }
  
  .footerBottom {
    text-align: center;
    font-size: 0.9rem;
    opacity: 0.8;
  }
  
  /* Media Queries */
  @media (max-width: 768px) {
    .header .container {
      flex-direction: column;
      gap: 1rem;
    }
  
    .navigation {
      flex-wrap: wrap;
      justify-content: center;
    }
  
    .heroContainer {
      grid-template-columns: 1fr;
      text-align: center;
    }
  
    .heroButtons {
      justify-content: center;
    }
  
    .heroIcon {
      width: 150px;
      height: 150px;
    }
  
    .securityGrid,
    .configGrid {
      grid-template-columns: 1fr;
    }
  }
  
  @media (max-width: 480px) {
    .heroTitle {
      font-size: 1.8rem;
    }
  
    .sectionTitle {
      font-size: 1.5rem;
    }
  
    .cardGrid3Cols,
    .cardGrid2Cols {
      grid-template-columns: 1fr;
    }
  }